.searchBar {
  margin-bottom: 1rem;
  padding: 1rem;
  background-color: #fff;
  -webkit-box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
          box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
}

.label, .input {
  display: block;
  width: 100%;
}

.input {
  padding-top: 1rem;
  padding-bottom: .5rem;
  border: 0;
  border-bottom: 1px solid #9e9e9e;
}

.input:focus {
  outline: none;
}



